import loading from "@/common/mixin/loading.js"

import uniNavBar from "@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue"
import price from "@/components/common/price.vue"
import uniNumberBox from "@/components/uni-ui/uni-number-box/uni-number-box.vue"

import {mapState,mapGetters,mapActions,mapMutations} from "vuex"

import commonList from "@/components/common/common-list.vue"
import skuPopup from '@/components/cart/sku-popup.vue';

export default {
	mixins:[loading],
	components: {
		uniNavBar,
		price,
		uniNumberBox,
		commonList,
		skuPopup
	},
	data() {
		return {
			isedit: false,
			hotList:[]
		}
	},
	computed: {
		...mapState({
			list:state=>state.cart.list
		}),
		...mapGetters([
			'checkedAll',
			'totalPrice',
			'disableSelectAll'
		])
	},
	onShow() {
		this.getData()
	},
	methods:{
		...mapActions([
			'doSelectAll',
			'doDelGoods',
			'doShowPopup',
		]),
		...mapMutations([
			'selectItem',
			'initCartList'
		]),
		changeNum(e,item,index){
			if (item.num === e) return;
			uni.showLoading({
				title:"加载中..."
			})
			this.$H.post('/cart/updatenumber/'+item.id,{
				num:e
			},{
				token:true
			}).then(res=>{
				console.log(res);
				item.num = e
				uni.hideLoading()
			})
		},
		// 订单结算
		orderConfirm(){
			uni.navigateTo({
				url: '../order-confirm/order-confirm'
			});
		},
		showPopup(index){
			if (this.isedit) {
				this.doShowPopup(index)
			}
		},
		// 获取数据
		getData(){
			// 获取购物车数据
			this.$H.get('/cart',{},{
				token:true,
				toast:false
			}).then(res=>{
				this.initCartList(res)
			})
			// 获取热门推荐
			this.$H.get('/goods/hotlist').then(res=>{
				this.hotList = res.map(v=>{
					return {
					  id:v.id,
					  cover:v.cover,
					  title:v.title,
					  desc:v.desc,
					  oprice:v.min_oprice,
					  pprice:v.min_price
					}
				})
			})
		}
	}
}
